import "./App.css";
import Header from "./components/header";
import SideNav from "./components/sideNav";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Design from "./components/design";
import Activity from "./components/activity";
import Company from "./components/companyProfile";
import Information from "./components/infromation";
import Products from "./components/products";
import Quality from "./components/quality";
import References from "./components/references";
import Almon from "./components/subProducts/almon";
import Process from "./components/subProducts/process";
import Rinser from "./components/subProducts/rinser";
import Fillers from "./components/subProducts/fillers";
import Carbonated from "./components/subProducts/carbonated";
import Tunnel from "./components/subProducts/tunnel";
import Handle from "./components/subProducts/handling";
import Video from "./components/video";

function App() {
  return (
    <div className="App">
      <Router>
        <Header />
        <div className="container">
          <SideNav />
          <div className="main-content">
            <Switch>
              <Route exact path="/" component={Design} />
              <Route exact path="/design" component={Design} />
              <Route exact path="/activity" component={Activity} />
              <Route exact path="/products" component={Products} />
              <Route exact path="/quality" component={Quality} />
              <Route exact path="/references" component={References} />
              <Route exact path="/information" component={Information} />
              <Route exact path="/almon" component={Almon} />
              <Route exact path="/process" component={Process} />
              <Route exact path="/rinsers" component={Rinser} />
              <Route exact path="/fillers" component={Fillers} />
              <Route exact path="/carbonated" component={Carbonated} />
              <Route exact path="/tunnel" component={Tunnel} />
              <Route exact path="/handling" component={Handle} />
              <Route exact path="/video" component={Video} />
              <Route exact path="/profile" component={Company} />
            </Switch>
          </div>
        </div>
      </Router>
    </div>
  );
}

export default App;
